.container {
  width: 1120px;
  margin: 0 auto;
}
.container:before,
.container:after {
    content: " ";
    display: table;
}
.container:after {
    clear: both;
}
.container-fluid {
  width: 100%;
}
.row {
  width: 100%;
  margin-left: -15px;
  margin-right: -15px;
  display: table;
}
.row:before,
.row:after {
    content: " ";
    display: table;
}
*:before,
*:after {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}
[class^="col-"] {
  float: left;
  box-sizing: border-box;
  position: relative;
  min-height: 1px;
  padding-left: 15px;
  padding-right: 15px;
}
